<template>
   <a :href="link" class="register__option--btn">
      <div class="register__option register__option--google">
        <img src="../assets/google.webp" alt="google logo" class="register__option--logo"/>
        Google
      </div>
    </a>
  </template>
  <script>
  export default {
    name: "registerOptions",
    data() {
      return {
        link: "",
      };
    },
    created() {
        fetch(localStorage.server + '/user/google/getLink?scope=https://www.googleapis.com/auth/gmail.send'
        ,{
            method: 'GET'
          }
        )
          .then((res) => {
            console.log(res);
            if (res.status === 200) {
              res.text().then((ans) => {
                this.link = ans;
              })
            }
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
    },
  };
  </script>
